/* Reset */
body {
  margin: 0;
  padding: 0;
  font-family: 'Poppins', sans-serif;
  overflow-x: hidden;

  /* Pink + Red Gradient */
  background: linear-gradient(
    -45deg,
    #ff4e7a,
    #ff758c,
    #ff7eb3,
    #ff3d68
  );
  background-size: 400% 400%;
  animation: gradientFlow 10s ease infinite;

  color: white;
}

/* Smooth gradient animation */
@keyframes gradientFlow {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

/* Optional: Make content slightly glow */
h1, h2, h3, p {
  text-shadow: 2px 2px 12px rgba(0, 0, 0, 0.3);
}

/* Make buttons stylish */
button {
  padding: 12px 20px;
  border-radius: 30px;
  border: none;
  cursor: pointer;
  background: white;
  color: #ff3d68;
  font-weight: bold;
  transition: 0.3s ease;
  box-shadow: 0px 6px 18px rgba(0, 0, 0, 0.25);
}

button:hover {
  transform: scale(1.08);
  background: #ff3d68;
  color: white;
}

/* Inputs */
input, textarea {
  padding: 10px;
  border-radius: 20px;
  border: none;
  width: 260px;
  text-align: center;
  outline: none;
  box-shadow: 0px 5px 12px rgba(0, 0, 0, 0.2);
}
.date-page {
  min-height: 100vh;
  background: linear-gradient(135deg, #ff4e8a, #ff99cc);
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 60px;
  font-family: sans-serif;
}

.date-form {
  display: flex;
  flex-direction: column;
  width: 300px;
  gap: 10px;
}

.date-form input,
.date-form select {
  padding: 10px;
  border-radius: 8px;
  border: none;
}

.date-form button {
  padding: 10px;
  background-color: #ff4e8a;
  color: white;
  border: none;
  border-radius: 8px;
  cursor: pointer;
}

.result-card {
  margin-top: 30px;
  background: linear-gradient(135deg, #ff4e8a, #df03d1);
  padding: 20px;
  border-radius: 12px;
  width: 320px;
